.notification {
  border-bottom: 1px solid $gray;
  padding: 20px 0;
}

.notification .container {
  @include mediaQuery(tablet-and-up) {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
}

.notification__content {
  align-items: center;
  display: flex;

  > img {
    display: block;
    height: 50px;
    margin-right: 30px;
  }
}

.notification__text {
  margin: 0;
  margin-bottom: 10px;

  @include mediaQuery(tablet-and-up) {
    margin-bottom: 0;
  }
}